 *
 * Replace them with your own styles or remove the file completely.
 */

 .modal-disable #wptravel-block-trip-enquiry{
    display: block;
  }

 #wp-travel-enquiries{
    max-width: 100%;
    top: 0;
    background: #fff;
    padding: 0px;
    width: 100%;
    position: relative;
    display: block;
    border-radius: 5px;
    .wp-travel-form-field {
        margin: 10px 0;
        width: 100%;
        display: inline-block;
        label{
            width: 100%;
            display: block;
        }
        input{
            width: 100%;
            height: 40px;
        }
        input#wp-travel-enquiry-submit{
            height: auto;
        }
        .radio-checkbox-label input{
            height: auto;
        }
        input[type=checkbox]{
            width: auto;
        }
        textarea {
            width: inherit;
        }
    }
 }

 #wptravel-block-trip-enquiry label[for="wp-travel-subscribe"],
 #wptravel-block-trip-enquiry label[for="wp-travel-enquiry-post-id"],
 #wptravel-block-trip-enquiry #wp-travel-enquiry-post-id {
    display: none;
}